import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '',
      redirect: '/im'
    },
    {
      path: '/hr',
      name: 'hr',
      component: () => import('@/views/hr/hr.vue'),
      meta: {
        title: ''
      }
    },
    {
      path: '/im',
      name: 'im',
      component: () => import('@/views/im/im.vue'),
      meta: {
        title: ''
      }
    },
    {
      path: '/ad',
      name: 'ad',
      component: () => import('@/views/ad/ad.vue'),
      meta: {
        title: ''
      }
    },{
      path: '/app',
      name: 'app',
      component: () => import('@/views/app/app.vue'),
      meta: {
        title: ''
      }
    }
  ]
})



